.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden;border-radius:0.5rem;transition:all 0.5s ease-out}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);transition:-webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95), -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:1}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:center;border-radius:0.5rem}.menu-item .content{height:auto;padding:1rem 1.5rem;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background:linear-gradient(180deg, #fcfcf3, #fcfcf3, #fdfdf2, #fdfdf2, #fefef1, #fefef1, ivory, ivory);opacity:0.7;position:absolute;border-radius:0.5rem}.menu-item .content .title{font-size:clamp(1rem, 2vw, 2.4rem);margin-bottom:clamp(0.5rem, 1.2vw, 1.5rem);font-family:'Style Script', cursive;font-weight:lighter;margin:0 auto;color:#000;text-transform:capitalize}.menu-item .content .subtitle{font-weight:300;font-size:clamp(0.6rem, 0.8vw, 0.8rem);margin:0 auto}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.homepage{display:flex;flex-direction:column;align-items:center;padding:0}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:2px;line-height:clamp(1rem, 1.2vw, 3.4rem);padding:0 1rem;margin:0.5rem;font-size:clamp(0.5rem, 1.2vw, 0.8rem);font-weight:200;background-color:#091c41;color:white;text-transform:uppercase;border:none;font-family:'Source Sans Pro', sans-serif;font-weight:300;border-radius:0.3rem;transition:all 0.3s ease;border:1px solid #fff;cursor:pointer;box-shadow:0 2px 8px rgba(0,0,0,0.26)}.custom-button:hover{background-color:white;color:black;border:1px solid black;font-weight:300}.custom-button:hover:focus{outline:none}.custom-button:hover:focus:active{-webkit-transform:scale(0.9);transform:scale(0.9);box-shadow:inset 0 0 10px #000}.custom-button.inverted{margin-top:1rem;background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.collection-item{width:22vw;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:90%;height:95%;border-radius:0.5rem;background-size:cover;background-position:center;margin-bottom:5px;box-shadow:0 2px 8px rgba(0,0,0,0.26)}.collection-item .custom-button{width:80%;position:absolute;display:flex;justify-content:center;align-items:center;top:16rem;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{display:flex}.collection-item .collection-footer{width:90%;height:5%;display:flex;justify-content:space-between;font-size:clamp(0.6rem, 2vw, 1.6rem)}.collection-item .collection-footer .name{width:90%;margin-bottom:1rem;font-size:clamp(0.6rem, 2vw, 1.2rem)}.collection-item .collection-footer .price{width:10%;font-size:clamp(0.6rem, 2vw, 1.2rem);margin-right:1rem}

.collection-preview{display:flex;flex-direction:column;margin:0 auto;margin-bottom:2rem;width:100%}.collection-preview .title{font-size:clamp(3rem, 3.5vw, 3.5rem);margin-bottom:clamp(0.5rem, 1.2vw, 1.5rem);font-family:'Style Script', cursive;font-weight:lighter}.collection-preview .preview{display:flex;justify-content:space-between}

.collections-overview{display:flex;flex-direction:column}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-family:'Style Script', cursive;font-size:clamp(3rem, 4.2vw, 4rem);font-weight:lighter;color:#000;margin:0 auto}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}

.cart-icon{width:3rem;height:3rem;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:2rem;height:2rem}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%;box-shadow:0 2px 8px rgba(0,0,0,0.26)}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:15rem;height:22rem;display:flex;flex-direction:column;padding:1.4rem;border:1px solid black;background-color:white;top:6rem;right:3rem;z-index:5;border-radius:0.5rem}.cart-dropdown .empty-message{margin:3rem auto;font-size:1rem}.cart-dropdown .cart-items{height:15rem;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}.cart-items::-webkit-scrollbar{width:6px;height:6px}.cart-items::-webkit-scrollbar-thumb{background:#8b92a5;border-radius:10px}.cart-items::-webkit-scrollbar-thumb:hover{background:#b5ccf6}.cart-items::-webkit-scrollbar-track{background:#fefef1}

.header{height:4.6rem;width:100%;display:flex;justify-content:space-between;align-items:center;margin-bottom:1.6rem}.header .companyLogo{display:flex;flex-direction:row;align-items:center}.header .companyLogo .logo-container{height:100%;width:auto;padding:1rem;-webkit-filter:drop-shadow(2px 2px 3px rgba(0,0,0,0.5));filter:drop-shadow(2px 2px 3px rgba(0,0,0,0.5))}.header .companyLogo .logo-text{font-size:clamp(2.8rem, 4.2vw, 4rem);vertical-align:middle;margin:0}.header .companyLogo .company{font-family:'Style Script', cursive;font-size:clamp(2rem, 3.2vw, 3rem);font-weight:lighter;color:#000;margin:0}.options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.options .option{text-decoration:none;background-color:#091c41;color:#fff;border:1px solid #091c41;font-size:clamp(0.5rem, 1vw, 0.6rem);text-transform:uppercase;font-weight:300;letter-spacing:2px;padding:0.5rem 1rem;border-radius:0.3rem;transition:all 0.3s ease;cursor:pointer;margin-left:clamp(0.2rem, 0.8vw, 1rem);box-shadow:0 2px 8px rgba(0,0,0,0.26)}.options .option.option:hover{background-color:#0f3277;color:#fffff0;border:1px solid #fffff0}.options .option.option:active{-webkit-transform:scale(0.9);transform:scale(0.9);box-shadow:inset 0 0 10px #fffff0}.options .option.option:focus{outline:none}@media screen and (max-width: 800px){.header{height:6.4rem;display:flex;justify-content:center;align-items:center}.header .company{display:none}.header .options{width:100%}.header .options .option{font-size:clamp(0.5rem, 1vw, 0.6rem)}}

.group{position:relative;margin:45px 0;padding:0}.group .form-input{color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-20px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:1rem;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-20px;font-size:12px;color:#000}

.sign-in{width:30vw;min-width:200px;display:flex;flex-direction:column;margin:0 1rem}.buttons{display:flex;justify-content:space-between}h2{font-family:'Source Sans Pro', sans-serif;font-weight:400}@media screen and (max-width: 1200px){.buttons{display:flex;flex-wrap:wrap;flex-direction:column;justify-content:center;align-items:center}}@media screen and (max-width: 850px){.sign-in{width:100%;flex-direction:column;justify-content:center;align-items:center;margin:0}.buttons{display:flex;flex-wrap:wrap;flex-direction:column;justify-content:center;align-items:center}}

.sign-up{width:30vw;min-width:200px;display:flex;flex-direction:column;margin:0 1rem}.button{text-align:right}h2{font-family:'Source Sans Pro', sans-serif;font-weight:400}@media screen and (max-width: 850px){.sign-up{width:100%;flex-direction:column;justify-content:center;align-items:center}.button{display:flex;flex-direction:column;justify-content:center;align-items:center}}

.sign-in-and-sign-up{max-width:57rem;display:flex;justify-content:space-between;margin:2rem auto}@media screen and (max-width: 1200px){.sign-in-and-sign-up{width:100%;flex-direction:row;justify-content:space-around;margin:0}}@media screen and (max-width: 850px){.sign-in-and-sign-up{width:100%;flex-direction:column;justify-content:center;align-items:center;margin:0}}

.checkout-item{width:100%;display:flex;min-height:6rem;border-bottom:1px solid darkgrey;padding:1rem 0;font-size:1.4rem;font-family:'Source Sans Pro', sans-serif;font-weight:300;align-items:center}.checkout-item .image-container{width:23%;padding-right:1rem}.checkout-item .image-container img{width:100%;height:100%;border-radius:0.5rem;box-shadow:0 2px 8px rgba(0,0,0,0.26)}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%;font-size:clamp(0.4rem, 2vw, 1.6rem)}.checkout-item .price{margin-right:auto;vertical-align:middle}.checkout-item .quantity{display:flex;margin:0 auto}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px;vertical-align:middle}.checkout-item .remove-button{margin-right:auto;cursor:pointer;color:#b40600}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:3rem auto}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:uppercase;font-size:clamp(0.4rem, 2vw, 1.4rem);width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:2rem;margin-left:auto;font-size:clamp(1rem, 2.5vw, 2.3rem)}.checkout-page .button{margin-left:auto;margin-top:3rem;box-shadow:0 2px 8px rgba(0,0,0,0.26)}.checkout-page .test-warning{text-align:center;margin-top:4rem;font-size:clamp(0.8rem, 2vw, 1.2rem);color:red}@media screen and (max-width: 1200px){.checkout-page{width:75%}}@media screen and (max-width: 800px){.checkout-page{width:95%}}

html {
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: #a1acb8 #000;
}
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 1rem 4rem;
  font-family: 'Raleway', sans-serif;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-weight: 300;
  overflow: auto;
  background: linear-gradient(
    180deg,
    #fcfcf3,
    #fcfcf3,
    #fdfdf2,
    #fdfdf2,
    #fefef1,
    #fefef1,
    #fffff0,
    #fffff0
  );
  -webkit-animation: fadeInAnimation ease-out 3s 1 forwards;
          animation: fadeInAnimation ease-out 3s 1 forwards;
}
@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
body::-webkit-scrollbar-thumb {
  background: #8b92a5;
  border-radius: 10px;
}
body::-webkit-scrollbar-thumb:hover {
  background: #b5ccf6;
}
body::-webkit-scrollbar-track {
  background: #fefef1;
}
a {
  text-decoration: none;
  color: #000;
}

